.overflow-td {
  overflow-y: auto;
  width: 200px;
  height: 66px;
  word-break: break-word;
}

.value {
  font-weight: bold;
  opacity: 0.6;
}

.table-styling {
  margin-right: 5px;
  margin-left: -5px;
}

.breadcrumbs {
  margin-left: 0 !important;
  color: white;
  width: 800px;
}

.breadcrumbs-item-text {
  max-width: 200px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.centered-input>>>input {
  text-align: center;
}

.cursor-pointer {
  cursor: pointer;
  color: #1867c0;
}

.background-fault-true {
  background-color: #fbb93e;
}

.background-enable-false {
  background-color: #666666;
}
